<template>
  <Form
    ref="formInline"
    :model="formInline"
    :rules="ruleInline"
    inline
    class="form-flex"
  >
    <h1 style="margin-bottom: 20px">會員登入</h1>
    <FormItem prop="user">
      <Input type="text" v-model="formInline.user" placeholder="Username">
        <Icon type="ios-person-outline" slot="prepend"></Icon>
      </Input>
    </FormItem>
    <FormItem prop="password">
      <Input
        type="password"
        v-model="formInline.password"
        placeholder="Password"
      >
        <Icon type="ios-lock-outline" slot="prepend"></Icon>
      </Input>
    </FormItem>
    <div>
      <FormItem>
        <Button type="primary" class="buttonsize">註冊</Button>
      </FormItem>
      <FormItem>
        <Button
          type="success"
          @click="handleSubmit('formInline')"
          class="buttonsize"
          >登入</Button
        >
      </FormItem>
    </div>
  </Form>
</template>
<script>
export default {
  data() {
    return {
      formInline: {
        user: "",
        password: "",
      },
      ruleInline: {
        user: [
          {
            required: true,
            message: "Please fill in the user name",
            trigger: "blur",
          },
        ],
        password: [
          {
            required: true,
            message: "Please fill in the password.",
            trigger: "blur",
          },
          {
            type: "string",
            min: 6,
            message: "The password length cannot be less than 6 bits",
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    handleSubmit(name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.$Message.success("Success!");
        } else {
          this.$Message.error("Fail!");
        }
      });
    },
  },
};
</script>

<style lang="scss">
.form-flex {
  display: flex !important;
  flex-direction: column;
}
.ivu-form-item-content {
  min-width: 230px;
  min-height: 50px;
  margin-bottom: 10px;
}
.ivu-input-default {
  min-height: 50px;
}
.ivu-icon-ios-person-outline,
.ivu-icon-ios-lock-outline {
  font-size: 30px !important;
}
.ivu-input {
  font-size: 32px !important;
}
.buttonsize {
  min-width: 130px !important;
  min-height: 50px !important;
  font-size: 30px;
  margin-top: 100px;
}
.ivu-form-item-error-tip {
  font-size: 24px;
  margin-top: 3px;
}
</style>
